@charset "UTF-8";

/*Owl Carousel - Default theme*/
.owl-theme .owl-controls {
	margin-top:10px;
	text-align:center;
	-webkit-tap-highlight-color:transparent;
}

.owl-theme .owl-controls .owl-nav [class*=owl-] {
	color:#fff;
	font-size:14px;
	margin:5px;
	padding:4px 7px;
	background:#ccc;
	display:inline-block;
	cursor:pointer;
	-webkit-border-radius:3px;
	-moz-border-radius:3px;
	border-radius:3px;
}

.owl-theme .owl-controls .owl-nav [class*=owl-]:hover {
	background:#666;
	color:#fff;
	text-decoration:none;
}

.owl-theme .owl-controls .owl-nav .disabled {
	opacity:.5;
	cursor:default;
}

.owl-theme .owl-dots .owl-dot {
	display:inline-block;
	zoom:1;
	*display:inline;
}

.owl-theme .owl-dots .owl-dot span {
	width:10px;
	height:10px;
	margin:5px 7px;
	background:#ccc;
	display:block;
	-webkit-backface-visibility:visible;
	-webkit-transition:opacity 200ms ease;
	-moz-transition:opacity 200ms ease;
	-ms-transition:opacity 200ms ease;
	-o-transition:opacity 200ms ease;
	transition:opacity 200ms ease;
	-webkit-border-radius:30px;
	-moz-border-radius:30px;
	border-radius:30px;
}

.owl-theme .owl-dots .owl-dot.active span,.owl-theme .owl-dots .owl-dot:hover span {
	background:#666;
}

/* *  Owl Carousel - Animate Plugin */
.owl-carousel .animated {
	-webkit-animation-duration:1000ms;
	animation-duration:1000ms;
	-webkit-animation-fill-mode:both;
	animation-fill-mode:both;
}

.owl-carousel .owl-animated-in {
	z-index:0;
}

.owl-carousel .owl-animated-out {
	z-index:1;
}

.owl-carousel .fadeOut {
	-webkit-animation-name:fadeOut;
	animation-name:fadeOut;
}

@-webkit-keyframes fadeOut {
	0% {
		opacity:1;
	}
	
	100% {
		opacity:0;
	}
}

@keyframes fadeOut {
	0% {
		opacity:1;
	}
	
	100% {
		opacity:0;
	}
}

/* * 	Owl Carousel - Auto Height Plugin */
.owl-height {
	-webkit-transition:height 500ms ease-in-out;
	-moz-transition:height 500ms ease-in-out;
	-ms-transition:height 500ms ease-in-out;
	-o-transition:height 500ms ease-in-out;
	transition:height 500ms ease-in-out;
}

/* *  Core Owl Carousel CSS File */
.owl-carousel {
	display:none;
	width:100%;
	-webkit-tap-highlight-color:transparent;
/* position relative and z-index fix webkit rendering fonts issue */
	position:relative;
	z-index:1;
}

.owl-carousel .owl-stage {
	position:relative;
	-ms-touch-action:pan-Y;
}

.owl-carousel .owl-stage:after {
	content:".";
	display:block;
	clear:both;
	visibility:hidden;
	line-height:0;
	height:0;
}

.owl-carousel .owl-stage-outer {
	position:relative;
	overflow:hidden;
/* fix for flashing background */
	-webkit-transform:translate3d(0px,0px,0px);
}

.owl-carousel .owl-controls .owl-nav .owl-prev,.owl-carousel .owl-controls .owl-nav .owl-next,.owl-carousel .owl-controls .owl-dot {
	cursor:pointer;
	cursor:hand;
	-webkit-user-select:none;
	-khtml-user-select:none;
	-moz-user-select:none;
	-ms-user-select:none;
	user-select:none;
}

.owl-carousel.owl-loaded {
	display:block;
}

.owl-carousel.owl-loading {
	opacity:0;
	display:block;
}

.owl-carousel.owl-hidden {
	opacity:0;
}

.owl-carousel .owl-refresh .owl-item {
	display:none;
}

.owl-carousel .owl-item {
	position:relative;
	min-height:1px;
	float:left;
	-webkit-tap-highlight-color:transparent;
	-webkit-touch-callout:none;
	-webkit-user-select:none;
	-moz-user-select:none;
	-ms-user-select:none;
	user-select:none;
	padding:0 0 6px;
	text-align: center;

}

.owl-carousel .owl-item img {
/* 	display:block; */
/* 	width:100%; */
	margin: 0;
}

.owl-carousel.owl-text-select-on .owl-item {
	-webkit-user-select:auto;
	-moz-user-select:auto;
	-ms-user-select:auto;
	user-select:auto;
}

.owl-carousel .owl-grab {
	cursor:move;
	cursor:-webkit-grab;
	cursor:-o-grab;
	cursor:-ms-grab;
	cursor:grab;
}

.owl-carousel.owl-rtl {
	direction:rtl;
}

.owl-carousel.owl-rtl .owl-item {
	float:right;
}

/* No Js */
.no-js .owl-carousel {
	display:block;
}

/* * 	Owl Carousel - Lazy Load Plugin */
.owl-carousel .owl-item .owl-lazy {
	opacity:0;
	-webkit-transition:opacity 400ms ease;
	-moz-transition:opacity 400ms ease;
	-ms-transition:opacity 400ms ease;
	-o-transition:opacity 400ms ease;
	transition:opacity 400ms ease;
}

/*admin preview thumbnail styles */
.foogallery-owl-carousel a {
	display:inline-block;
	overflow:hidden;
}

.foogallery-owl-carousel a img {
	border-radius:0;
	display:inline-block;
}

/* Border styles */
.foogallery-owl-carousel.border-style-rounded a {
	border-radius:5px;
	-webkit-border-radius:5px;
	-moz-border-radius:5px;
}

.foogallery-owl-carousel.border-style-square-white a img{
	border:0px solid #FFF;
	-webkit-box-shadow:0 0 10px rgba(0,0,0,0.5);
	-moz-box-shadow:0 0 10px rgba(0,0,0,0.5);
	box-shadow:0 1px 5px rgba(0,0,0,0.5);
}

.foogallery-owl-carousel.border-style-circle-white a,.foogallery-owl-carousel.foogallery-lightbox-none.border-style-circle-white img {
	border-radius:50%;
	-webkit-border-radius:50%;
	-moz-border-radius:50%;
	border:5px solid #FFF;
	-webkit-box-shadow:0 0 10px rgba(0,0,0,0.5);
	-moz-box-shadow:0 0 10px rgba(0,0,0,0.5);
	box-shadow:0 1px 5px rgba(0,0,0,0.5);
	max-width:95%;
}

.foogallery-owl-carousel.border-style-circle-black a,.foogallery-owl-carousel.border-style-circle-black.foogallery-lightbox-none img {
	border-radius:50%;
	-webkit-border-radius:50%;
	-moz-border-radius:50%;
	border:3px solid #000;
	-webkit-box-shadow:0 0 10px rgba(0,0,0,0.5);
	-moz-box-shadow:0 0 10px rgba(0,0,0,0.5);
	box-shadow:0 1px 5px rgba(0,0,0,0.5);
}

.foogallery-owl-carousel.border-style-square-black a {
	border:3px solid #000;
	-webkit-box-shadow:0 0 10px rgba(0,0,0,0.5);
	-moz-box-shadow:0 0 10px rgba(0,0,0,0.5);
	box-shadow:0 1px 5px rgba(0,0,0,0.5);
}

.foogallery-owl-carousel.border-style-inset a {
	position:relative;
	max-width:100%;
}

.foogallery-owl-carousel.border-style-inset a::before {
	content:"";
	position:absolute;
	top:0;
	bottom:0;
	left:0;
	right:0;
	box-shadow:inset 0 0 20px #000;
	-moz-box-shadow:inset 0 0 20px #000;
	-webkit-box-shadow:inset 0 0 20px #000;
}

.foogallery-owl-carousel.foogallery-lightbox-none.border-style-inset .owl-item:before {
	content:'';
	position:absolute;
	top:0;
	bottom:6px;
	left:0;
	right:0;
	box-shadow:inset 0 0 30px rgba(0,0,0,.9);
	-moz-box-shadow:inset 0 0 30px rgba(0,0,0,.9);
	-webkit-box-shadow:inset 0 0 30px rgba(0,0,0,.9);
}

/*force no transition on hover so circle doesn't flash a square*/
.foogallery-owl-carousel.border-style-circle-white a img,.foogallery-owl-carousel.border-style-circle-black a img,.foogallery-owl-carousel.border-style-circle-white a:hover img,.foogallery-owl-carousel.border-style-circle-white a:hover img {
	opacity:1;
	-webkit-transition:all 0 ease 0;
	-moz-transition:all 0 ease 0;
	-ms-transition:all 0 ease 0;
	-o-transition:all 0 ease 0;
	transition:all 0 ease 0;
	max-height:95%;
}

